.spacing {
  padding: 90px 0;

  @include screen-above(768px) {
    padding: 150px 0;
  }
}

.pagination {
  position: relative;
  border-top: 1px solid var(--border);

  &--post {
    &::before {
      position: absolute;
      top: 0;
      left: 50%;
      width: 1px;
      height: 100%;
      background-color: var(--border);
      content: '';
      transform: translateX(-50%);
    }
  }
}

.pagination-item {
  padding: 45px 0;
  font-size: 1.25rem;
  font-weight: 700;
  justify-self: start;

  &--next {
    text-align: right;
    grid-column: 2 / -1;
    justify-self: end;
  }

  span {
    display: block;
    font-size: 1rem;
    font-weight: 400;
    line-height: 2;
    color: var(--gray);
  }
}

.visually-hide {
  position: absolute;
  top: 0;
  left: -9999px;
  clip: rec(0, 0, 0, 0);
  pointer-events: none;
}

.category {
  color: var(--gray);
}

.active {
  text-decoration: underline;
}